    background-size: cover;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    /* color: #2c3e50; */
    /* overflow: scroll; */
    /* overflow-x: hidden; */
}
div {
    width: inherit;
    /* backdrop-filter: blur(4px); */
}
